<template>
	<v-card
		class="overflow-hidden"
		outlinedx
		max-height="200px"
		@click="cardClickHandler"
	>
		<v-icon @click.stop="handleStarAction" color="cyan">{{ icon }}</v-icon>
		<v-list-item>
			<v-list-item-avatar tile size="100">
				<v-img contain :src="item.image_url" />
			</v-list-item-avatar>
			<v-card elevation="0">
				<v-card-title class="text-h6 mb-1">
					<p>
						{{ item.name }}
					</p>
				</v-card-title>
				<v-card-subtitle>
					<div class="ellipsis">
						<div>
							<p>{{ item.description }}</p>
						</div>
					</div>
				</v-card-subtitle>
			</v-card>
		</v-list-item>
	</v-card>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
import router from "@/router/index"
import routeNames from "@/router/routeNames"
export default {
	props: { item: { type: Object, required: true } },
	computed: {
		...mapGetters("products", ["isFavourites"]),
		icon() {
			if (this.isFavourites(this.item.id)) {
				return "mdi-star"
			} else {
				return "mdi-star-outline"
			}
		},
	},
	methods: {
		...mapActions("products", ["addToFavourites", "removeFromFavourites"]),
		handleStarAction() {
			if (!this.isFavourites(this.item.id)) {
				this.addToFavourites(this.item)
			} else {
				this.removeFromFavourites(this.item.id)
			}
		},
		cardClickHandler() {
			router.push({
				name: routeNames.productPage,
				params: { id: this.item.id },
			})
		},
	},
}
</script>

